@media (prefers-reduced-motion: no-preference) {
	.Balloon-logo1 {
		animation: Balloon-logo-float1 infinite 20s linear;
		height: 500px;
	}
}

@media (prefers-reduced-motion: no-preference) {
	.moon {
		color: rgb(231, 182, 118);
		position: absolute;
		display: flex-start;
		flex-direction: column;
		align-items: center;
