@keyframes moonAnimation {
	0% {
		transform: translate(0px, -100px);
		opacity: 0;
	}
	25% {
		transform: translate(450, -180px);
		opacity: 0.4;
	}
	50% {
		transform: translate(900px, -200px);
		opacity: 0.4;
	}
	75% {
		transform: translate(1340px, -180px);
		opacity: 0.4;
	}
	100% {
		transform: translate(1800px, 0px);
		opacity: 0;
	}
}

@keyframes sunAnimation {
	0% {
		transform: translate(1800px, 0px);
		opacity: 0;
	}
	25% {
		transform: translate(1340px, -180px);
		opacity: 0.4;
	}
	50% {
		transform: translate(900px, -200px);
		opacity: 0.4;
	}
	75% {
		transform: translate(450, -180px);
		opacity: 0.4;
	}
	100% {
		transform: translate(0px, -100px);
		opacity: 0;
	}
}

