body {
	background: #1A1A1A;
}

.socialsy {
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
	width: 80px;
	height: 80px;
	margin: -20px 0 0 30px;
	border: none;
	font: normal 100%/normal Arial, Helvetica, sans-serif;
	-o-text-overflow: clip;
	text-overflow: clip;
	float: left;
	margin-top: 30px;
	margin-left: 30px;
	cursor: pointer;
}

.socialsy div {
	width: 80px;
	height: 80px;
	font-size: 40px;
	text-align: center;
	line-height: 80px;
	color: #3a5795;
	font-weight: bold;
	display: flex;
	flex-direction: row;
}

.bgfc {
	border: 3px solid #ffffff;
	background-color: rgba(255, 255, 255, 0.3);
}

.bgfc div {
	color: #ffffff;
}

.bgfc:hover {
	border: 3px solid #ffffff;
	background-color: rgba(202, 184, 195, 0.8);
	transition: all .7s;
}

.bgfc:hover div {
	color: #fff;
	transition: all .7s;
}